<i18n>
{
  "en": {
    "ReturnBattery": "Return Portable battery",
    "putCharging": "Please put the charging treasure from the above position in time",
    "unit": "S",
    "Reminder": "Reminder",
    "unreturnedBatteryToExit": "Detected you did not return the battery, are you sure to exit?",
    "OK": "OK",
    "Cancel": "Cancel",
    "reScanQRcode": "No charging treasure is put in, please re-scan the qr code of the cabinet",
    "unreturnedBattery": "You currently do not have unreturned battery pack",
    "equipmentIsFull": "The card slot of this equipment is full, please change the equipment to use.",
    "RestitutionFailure": "Restitution failure"
  },
  "zh": {
    "ReturnBattery": "归还充电宝",
    "putCharging": "请及时从上图位置放入充电宝",
    "unit": "秒",
    "Reminder": "提示",
    "unreturnedBatteryToExit": "检测到你未归还充电宝，确定退出吗",
    "OK": "好的",
    "Cancel": "取消",
    "reScanQRcode": "检测到没有充电宝放入，请重新扫描机柜二维码",
    "unreturnedBattery": "您当前没有未归还的充电宝哦",
    "equipmentIsFull": "该设备卡槽已满，请换台设备使用",
    "RestitutionFailure": "还入失败"
  }
}
</i18n>

<template>
  <div class="takingCharger" ref="takingCharger">
    <head-top :title='$t("ReturnBattery")' :custom-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <img src="../../../assets/scanning_images/baby-icon.png" alt="">
      <span>{{ $t('putCharging') }}</span>
      <div class="countdown">
        <b>
          <strong>{{time}}</strong>{{ $t('unit') }}
        </b>
        <div class="countdown-bg"></div>
      </div>
      <!-- <div class="describe">如未吐出充电宝，请耐心等待1-3分钟，如仍未吐出，请重新租借或联系客服</div> -->
    </div>

    <message-box  :id="9"
                  icon="noreturn"
                  :title="$t('Reminder')"
                  :describe="$t('unreturnedBatteryToExit')"
                  :confirm="$t('OK')"
                  :cancel="$t('Cancel')"
                  @boxHandle="boxHandle"></message-box>
    <message-box  :id="10"
                  icon="scan"
                  :title="$t('Reminder')"
                  :describe="$t('reScanQRcode')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>
    <message-box  :id="11"
                  icon="listIcon"
                  :title="$t('Reminder')"
                  :describe="$t('unreturnedBattery')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <toast :toastTip.sync="toastTip"
           :describe="$t('equipmentIsFull')"
           position="bottom"></toast>
    <toast :toastTip.sync="toastbox"
           :describe="$t('RestitutionFailure')"
           position="bottom"></toast>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import headTop from '../../../components/Header/index'
import messageBox from '../../../components/MessageBox/index'
import toast from '../../../components/Toast/index'
import { errorCode } from '../../../components/ErrorCode/abnormalCode.js'
export default {
  name: 'takingCharger',
  data () {
    return {
      timer: null, // 定时器
      timer2: null, // 计时器
      time: 60, // 时间
      toastTip: false,
      toastbox: false,
      timer3: null // 计时器 10s
    }
  },

  components: {
    headTop,
    messageBox,
    toast
  },

  computed: {
    ...mapGetters([ 'chargerId' ])
  },

  mounted () {
    this.$nextTick(() => {
      console.log(document.documentElement.clientHeight)
      this.$refs.takingCharger.style.height = document.documentElement.clientHeight
    })
    this.countdown()

    this.dataInteractionResponse(data => {
      // 归还充电宝返回
      if (data.msgType === '0x5212') {
        if (data.result) {
          this.$router.push({name: 'home', params: {payTip: 3}})
        } else {
          let timer
          switch (data.data.errorCode) {
            case '0x830204':
              this.toastTip = true
              timer = setTimeout(() => {
                this.$router.back()
                clearTimeout(timer)
              }, 2000)
              break
            case '0x830203':
              this.setGlobalBox({bol: true, id: 11})
              break
            default:
              if (!errorCode[data.data.errorCode]) {
                this.toastbox = true
              }
              timer = setTimeout(() => {
                this.$router.back()
                clearTimeout(timer)
              }, 2000)
          }
        }
      } else if (data.msgType === '0x5222' && data.result) {
        if (data.data[0].pay_state === 2) {
          clearInterval(this.timer3)
          this.dataInteractionRequest({
            msgType: '0x5211',
            no: data.data[0].no
          })
          this.$router.push({path: '/restituteSuccessfully'})
        }
      }
    })

    // 归还充电宝请求
    this.dataInteractionRequest({
      msgType: '0x5211',
      imei: this.chargerId.imei
    })
    this.returnforword()
    if (this.ISDEBUG) {
      // this.timer2 = setTimeout(() => {
      //   this.$http('/scanning/2').then(res => {
      //     if (res.data.result) {
      //       this.$router.push({name: 'home', params: {payTip: 3}})
      //     }
      //   })
      // }, 400)
      // window.dataInteractionResponse('{"msgType":"0x5214","result":true,"data":{"type":"RETURN","imei":"866157037268639","no":"LD20190114120709","lentTime":"1547438840000","lentMerchantName":"亓行","returnTime":"1547439968000","returnMerchantName":"亓行","fee":"1100"}}')
    }
  },

  methods: {
    ...mapActions([
      'setGlobalBox'
    ]),
    // 检测提前归还充电宝
    returnforword () {
      this.timer3 = setInterval(() => {
        this.dataInteractionRequest({
          msgType: '0x5221',
          type: 1,
          page: 1,
          count: 1
        })
      }, 10000)
    },
    countdown () {
      this.timer = setInterval(() => {
        if (--this.time <= 0) {
          clearInterval(this.timer)
          this.setGlobalBox({bol: true, id: 10})
        }
      }, 1000)
    },

    // 弹框
    boxHandle (confirm, cancel, id) {
      switch (id) {
        case 9:
          this.setGlobalBox({bol: false, id: 0})
          break
        case 10:
          this.setGlobalBox({bol: false, id: 0})
          if (confirm) {
            this.$router.back()
          }
          break
        case 11:
          this.setGlobalBox({bol: false, id: 0})
          break
      }
      if (confirm) {
        this.$router.back()
      }
    },

    // 返回按钮
    goBackHander () {
      this.setGlobalBox({bol: true, id: 9})
    }
  },

  beforeDestroy () {
    clearInterval(this.timer)
    clearTimeout(this.timer2)
    clearTimeout(this.timer3)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@keyframes rotate {
  0%{
    transform: rotate(0deg)
  }
  50% {
    transform: rotate(180deg)
  }
  100%{
    transform: rotate(360deg)
  }
}
.takingCharger{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    padding-top: 25px;
    img{
      height: 494px;
    }
    span{
      display: block;
      font-size: 32px;
      margin: 40px 0 50px 0;
    }
    .countdown{
      .wh(385px, 385px);
      position: relative;
      margin: 0 auto;
      b{
        .f-center;
        left: 55%;
        font-size: 30px;
        strong{
          font-size: 100px;
          margin-right: 30px;
        }
      }
      .countdown-bg{
        .fs;
        background: url('../../../assets/scanning_images/loading.png') 0 0 no-repeat;
        background-size: cover;
        animation: rotate 2s linear infinite;
      }
    }
    .describe{
      font-size: 26px;
      text-align: left;
      padding: 0 5%;
      color: #999999;
      margin-top: 40px;
    }
  }
}
</style>



